import logo_renato from "../Assets/logo_renato.svg";
import React from "react";
import { sections } from "../App";

export function Header( { scrollTo } ) {
    return <div className={'header'}>
        <img src={logo_renato} alt={'Renato Mazzilli, Level Designer'} />
        <div className={'nav-bar'}>
            {sections.map(( item, i ) => {
                return <NavItem doScrollCallback={() => scrollTo(item.ref_var)} {...item} key={i} />
            })}
            <a className={'btn'} href={'mailto:renmazzilli@gmail.com'}>
                Email me!
            </a>
        </div>
    </div>
}

function NavItem( { doScrollCallback, title, subtitle } ) {
    
    return <button className={'nav-item'} onClick={doScrollCallback}>
        <div className={'title'}>
            {subtitle}
        </div>
        <div className={'subtitle'}>
            {title}
        </div>
    </button>
}
