import React, { useState } from 'react';

const images = [ 6, 10, 15, 4 ]

export function imageCount( project ) {
    return images[project];
}

export function ProjectImage( { doEnlarge = false, project, image : number, title : alt_text } ) {
    const [ enlarged, enlarge ] = useState(false)
    
    
    if ( project >= images.length ) {
        return <></>;
    }
    
    if ( number >= images[project] ) {
        return <></>;
    }
    
    let f_n = number < 9 ? '0' + (number + 1) : '' + (number + 1);
    
    return <>
        <img
            onClick={
                () => {
                    if ( doEnlarge ) {
                        enlarge(!enlarged)
                    }
                }
            }
            className={enlarged ? 'enlarged' : null}
            src={require('./Assets/project_' + (project + 1) + '/foto_' + (f_n) + '.png')}
            alt={alt_text}
        />
        {!enlarged ? <></> :
            <div className={'img-space'}>
            
            </div>
        }
    </>
}
