import React, { Component, Fragment } from 'react';
import logo_renato from './Assets/logo_renato.svg';
import { imageCount, ProjectImage } from "./ImageHandler";
import { projects } from "./GameData";
import { personal } from "./PersonalData";
import renato_pic from "./Assets/renativiris.jpg";
import { Header } from "./Components/Header";

export const sections = [
    {
        title : 'Work',
        subtitle : 'Professional',
        section_code : 'work_professional',
        id : 1
    },
    {
        title : 'Work',
        subtitle : 'Personal',
        section_code : 'work_personal',
        id : 2
    },
    {
        title : 'Me',
        subtitle : 'About',
        section_code : 'about',
        id : 3
    },
]

export class App extends Component {
    
    state = {
        display : 'default',
    }
    
    constructor( props, context ) {
        super(props, context);
        for ( let i = 0; i < sections.length; i++ ) {
            sections[i].ref_var = React.createRef();
        }
    }
    
    scrollTo = ( e ) => {
        let yPos = e.current.getBoundingClientRect().y;
        window.scrollTo({
            top : yPos - 10,
            left : 0,
            behavior : 'smooth'
        })
        this.setState({ display : 'default' })
    }
    
    handleAction = ( e ) => {
        this.setState({ display : e })
        window.scrollTo({
            top : 0,
            left : 0,
            behavior : 'smooth'
        })
    }
    
    render() {
        const { display } = this.state;
        
        return (
            <Fragment>
                <Header scrollTo={this.scrollTo} />
                <div className={'app'}>
                    <div
                        style={
                            { transform : 'translate(' + (display === 'default' ? '0' : '-1020') + 'px,0)' }
                        }
                        className={'app-contents'}
                    >
                        <div className={'content-pane'}>
                            {sections.map(( item, i ) => {
                                return <Section
                                    callback={this.handleAction}
                                    key={i}
                                    {...item}
                                />
                            })}
                        </div>
                        
                        <AltPaneContent callback={this.handleAction} selected={display} />
                    </div>
                </div>
            </Fragment>
        );
    }
}

function SectionTitle( { title, subtitle, ref_var } ) {
    return <Fragment>
        {ref_var ?
            <h1 ref={ref_var}>
            
            </h1> : <Fragment />
        }
        
        <div className={'section-title'}>
            <div className={'title'}>
                {subtitle}
            </div>
            <div className={'subtitle'}>
                {title}
            </div>
        </div>
    </Fragment>
}

function SubSectionTitle( { title } ) {
    return <div className={'sub-section-title'}>
        <div className={'title'}>
            {title}
        </div>
    </div>
}

function Section( { title, subtitle, ref_var, id, callback } ) {
    return <div className={'section'}>
        <SectionTitle title={title} subtitle={subtitle} ref_var={ref_var} />
        <SectionContents id={id} callback={callback} />
    </div>
}

function ProjectCard( { title, id, callback } ) {
    return <div className={'project-card'} onClick={() => callback('project:' + id)}>
        <div className={'img-holder'}>
            <ProjectImage project={id} image={0} title={title} />
        </div>
        <div className={'title'}>
            {title}
        </div>
    </div>
}

function SectionContents( { id, callback } ) {
    switch ( id ) {
        case 1:
            return <ProfWorkSecContents callback={callback} />;
        case 2:
            return <PersWorkSecContents callback={callback} />
        case 3:
            return <AboutSecContents callback={callback} />;
        default:
            return <Fragment />
    }
}

function ProfWorkSecContents( { callback } ) {
    return <div className={'section-contents projects'}>
        {
            projects.filter(item => item.category === 'Professional').map(( item, i ) => {
                return (
                    <ProjectCard
                        callback={callback}
                        {...item}
                        key={i}
                    />
                )
            })
        }
    </div>
}

function PersWorkSecContents( { callback } ) {
    return <div className={'section-contents projects'}>
        {
            projects.filter(item => item.category === 'Personal').map(( item, i ) => {
                return (
                    <ProjectCard
                        callback={callback}
                        {...item}
                        key={i}
                    />
                )
            })
        }
    </div>
}

function AboutSecContents( { callback } ) {
    return <div className={'section-contents about'}>
        <div className={'description'}>
            <div>
                I'm a 23 years old guy who is passionate about play and make
                videogames and have a high level of attention to detail, willing
                to work on teams and focus on clear objectives.
            </div>
            <div>
                I have been playing games since I was 8 on my PSX, developing love
                from games like Legacy of Kayn; Soul Reaver, Crash Bandicoot and
                Silent Hill. I still play videogames and have the desire to make them,
                to create passion and love on new players, just like those games did with me.
            </div>
        </div>
        
        <div className={'other-junk'}>
            <img
                src={renato_pic}
                alt={'A young brilliant mind.'}
            />
            <button className={'btn'} onClick={() => callback('resume')}>
                View Resume
            </button>
        </div>
    </div>
}

function AltPaneContent( { callback, selected } ) {
    let type = selected.split(':')[0]
    switch ( type ) {
        case 'resume' :
            return <Resume callback={callback} />
        case 'project' :
            return <Project callback={callback} project_id={parseInt(selected.split(':')[1])} />
        default:
            return <Fragment> </Fragment>
    }
}

function Resume( { callback } ) {
    return <div className={'content-pane'}>
        <SubSectionTitle title={'Contact'} />
        
        <p>
            {
                personal.contact.map(( item, i ) => {
                    return <Fragment key={i}>
                        <span style={{ fontWeight : 'bold' }}> {item.name}: </span> {item.value} <br />
                    </Fragment>
                })
            }
        </p>
        
        <SubSectionTitle title={'Skills'} />
        
        <p>
            <span style={{ fontWeight : 'bold' }}> Level Design </span>
            <ul>
                {
                    personal.skills.map(( item, i ) => {
                        return <Fragment key={i}>
                            <li> {item} </li>
                        </Fragment>
                    })
                }
            </ul>
            <span style={{ fontWeight : 'bold' }}> Applications </span>
            <ul>
                {
                    personal.apps.map(( item, i ) => {
                        return <Fragment key={i}>
                            <li> {item} </li>
                        </Fragment>
                    })
                }
            </ul>
        </p>
        
        <SubSectionTitle title={'Professional Experience'} />
        
        <p>
            {
                personal.experience.map(( item, i ) => {
                    return <Fragment key={i}>
                        <span style={{ fontWeight : 'bold' }}> {item.name} </span> <br />
                        {item.start_date} - {item.end_date}: {item.role}<br /><br />
                    </Fragment>
                })
            }
        </p>
        
        <SubSectionTitle title={'Education'} />
        
        <p>
            {
                personal.education.map(( item, i ) => {
                    return <Fragment key={i}>
                        <span style={{ fontWeight : 'bold' }}> {item.institution}: </span> {item.title} ({item.date}) <br /><br />
                    </Fragment>
                })
            }
        </p>
        
        <SubSectionTitle title={'Languages'} />
        
        <p>
            {
                personal.languages.map(( item, i ) => {
                    return <Fragment key={i}>
                        {item.name}: {item.level}. <br />
                    </Fragment>
                })
            }
        </p>
        
        <div className={'button-section'}>
            <a
                className={'btn'}
                target={'_blank'}
                rel={'noopener noreferrer'}
                href={'cv_renato_mazzilli.pdf'}
                // download={'cv_renato_mazzilli.pdf'}
            >
                View PDF
            </a>
            
            <button
                className={'btn'}
                style={{ margin : 'auto' }}
                onClick={() => callback('default')}
            >
                Back
            </button>
        </div>
    </div>
}

function Project( { callback, project_id } ) {
    let { title, info } = projects[project_id];
    
    let gallery = [];
    for ( let i = 0; i < imageCount(project_id); i++ ) {
        gallery.push(<ProjectImage doEnlarge={true} project={project_id} image={i} title={title} />)
    }
    
    return <div className={'content-pane'}>
        
        <SectionTitle title={title} />
        
        {info.map(( item, i ) => {
            return <div key={i}>
                <SubSectionTitle title={item.name} />
                <p>
                    {item.text}
                </p>
            </div>
        })}
        
        <SubSectionTitle title={'Gallery'} />
        <div className={'gallery'}>
            {
                gallery.map(( item, i ) => {
                    return (<Fragment>
                        {/*<div className={'img-holder'} key={i}>*/}
                        {item}
                        {/*</div>*/}
                    </Fragment>)
                })
            }
        </div>
        
        <button
            className={'btn'}
            style={{ margin : 'auto' }}
            onClick={() => callback('default')}
        >
            Back
        </button>
    
    </div>
}
